// Отримати переклад за обраним ключем мови та ключем перекладу
function updateTranslations(languageKey) {
  var elements = document.getElementsByClassName('translate');

  for (var i = 0; i < elements.length; i++) {
    var element = elements[i];
    var translationKey = element.id.replace('-link', '');

    if (translations[languageKey].menu.hasOwnProperty(translationKey)) {
      element.textContent = translations[languageKey].menu[translationKey];
    }
  }

  // переклад для елементів зі секції "hero-section"
  var heroSection = document.querySelector('.hero-section');
  var heroElements = heroSection.getElementsByClassName('translate');

  for (var k = 0; k < heroElements.length; k++) {
    var heroElement = heroElements[k];
    var heroTranslationKey = heroElement.id.replace('-link', '');
    heroElement.textContent =
      translations[languageKey].hero[heroTranslationKey];
  }

  // переклад для елементів зі секції "about"
  var aboutSection = document.querySelector('.about');
  var aboutElements = aboutSection.getElementsByClassName('translate');

  for (var j = 0; j < aboutElements.length; j++) {
    var aboutElement = aboutElements[j];
    var aboutTranslationKey = aboutElement.id.replace('-link', '');
    aboutElement.textContent =
      translations[languageKey].about[aboutTranslationKey];
  }

  // переклад для елементів зі секції "location"
  var locationSection = document.querySelector('.location');
  var locationElements = locationSection.getElementsByClassName('translate');

  for (var j = 0; j < locationElements.length; j++) {
    var locationElement = locationElements[j];
    var locationTranslationKey = locationElement.id.replace('-link', '');
    locationElement.textContent =
      translations[languageKey].location[locationTranslationKey];
  }

  // переклад для елементів зі секції "footer"
  var footerSection = document.querySelector('.footer');
  var footerElements = footerSection.getElementsByClassName('translate');

  for (var s = 0; s < footerElements.length; s++) {
    var footerElement = footerElements[s];
    var footerTranslationKey = footerElement.id.replace('-link', '');
    footerElement.textContent =
      translations[languageKey].footer[footerTranslationKey];
  }

  // переклад для елементів зі секції "blogAboutStouns"
  var blogAboutStonesSection = document.querySelector('.blog');
  var blogAboutStonesElements =
    blogAboutStonesSection.getElementsByClassName('translate');

  for (var j = 0; j < blogAboutStonesElements.length; j++) {
    var blogAboutStonesElement = blogAboutStonesElements[j];
    var blogAboutStonesTranslationKey = blogAboutStonesElement.id.replace(
      '-link',
      ''
    );
    blogAboutStonesElement.textContent =
      translations[languageKey].blogAboutStones[blogAboutStonesTranslationKey];
  }
}

// Змінити мову
function changeLanguage(languageKey) {
  updateTranslations(languageKey);
}

// Отримати вибрану мову з випадаючого списку
var languageSelect = document.getElementById('language-select');
languageSelect.addEventListener('change', function () {
  var selectedLanguage = languageSelect.value;
  changeLanguage(selectedLanguage);
});

// Початкова ініціалізація перекладів
updateTranslations('en');
